"use client";

import { Card, CardContent, CardHeader, CardTitle, CardDescription } from "@/components/ui/card";
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import { Badge } from "@/components/ui/badge";
import { deliveries } from "@/lib/data";

const statusColors: { [key: string]: 'default' | 'secondary' | 'destructive' } = {
  'Completed': 'default',
  'Scheduled': 'secondary',
  'Delayed': 'destructive',
};

export default function DeliveriesPage() {
  const pickups = deliveries.filter(d => d.type === 'Pickup');
  const returns = deliveries.filter(d => d.type === 'Return');

  return (
    <Tabs defaultValue="pickups">
      <div className="flex items-center justify-between mb-4">
        <TabsList>
          <TabsTrigger value="pickups">Pickups</TabsTrigger>
          <TabsTrigger value="returns">Returns</TabsTrigger>
        </TabsList>
      </div>
      <TabsContent value="pickups">
        <Card>
          <CardHeader>
            <CardTitle>Scheduled Pickups</CardTitle>
            <CardDescription>Track and manage upcoming product pickups.</CardDescription>
          </CardHeader>
          <CardContent>
            <Table>
              <TableHeader>
                <TableRow>
                  <TableHead>Order ID</TableHead>
                  <TableHead>Customer</TableHead>
                  <TableHead>Scheduled Date</TableHead>
                  <TableHead>Status</TableHead>
                </TableRow>
              </TableHeader>
              <TableBody>
                {pickups.map((delivery) => (
                  <TableRow key={delivery.id}>
                    <TableCell className="font-mono text-xs">{delivery.orderId}</TableCell>
                    <TableCell className="font-medium">{delivery.customerName}</TableCell>
                    <TableCell>{delivery.scheduledDate.toLocaleDateString()}</TableCell>
                    <TableCell>
                      <Badge variant={statusColors[delivery.status]}>{delivery.status}</Badge>
                    </TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </CardContent>
        </Card>
      </TabsContent>
      <TabsContent value="returns">
        <Card>
          <CardHeader>
            <CardTitle>Scheduled Returns</CardTitle>
            <CardDescription>Track and manage upcoming product returns.</CardDescription>
          </CardHeader>
          <CardContent>
            <Table>
              <TableHeader>
                <TableRow>
                  <TableHead>Order ID</TableHead>
                  <TableHead>Customer</TableHead>
                  <TableHead>Scheduled Date</TableHead>
                  <TableHead>Status</TableHead>
                </TableRow>
              </TableHeader>
              <TableBody>
                {returns.map((delivery) => (
                  <TableRow key={delivery.id}>
                    <TableCell className="font-mono text-xs">{delivery.orderId}</TableCell>
                    <TableCell className="font-medium">{delivery.customerName}</TableCell>
                    <TableCell>{delivery.scheduledDate.toLocaleDateString()}</TableCell>
                    <TableCell>
                      <Badge variant={statusColors[delivery.status]}>{delivery.status}</Badge>
                    </TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </CardContent>
        </Card>
      </TabsContent>
    </Tabs>
  );
}
